import React,{FC} from 'react';
import styled from "styled-components";

interface RenderWeatherPresentProps {
    dataList: any,
}
type NumberKeys = {
    [key: string]: number;
}
type NumberOrStringKeys = {
    [key: string]: number|string;
}
type DayData = {
    main: NumberKeys,
    weather: NumberOrStringKeys[],
    wind: NumberKeys,
    clouds: NumberKeys,
}

const Weather = styled.div`
  
`;
const Card = styled.div`
  
`;

const WeatherCard: FC<{dayData: DayData}> = ({dayData}) => {
    return(
        <Card>
            <p>: {dayData.main.temp}F</p>
            <p> : {dayData.main.feels_like}F</p>
            <p> : {dayData.main.temp_min}F</p>
            <p> : {dayData.main.temp_max}F</p>
            <p>: {dayData.main.pressure} hPa</p>
            <p>: {dayData.main.humidity}%</p>
            <p>: {dayData.weather[0].description}</p>
            <p> :{dayData.wind.speed} /</p>
            <p> :{dayData.wind.deg}</p>
            <p>:{dayData.clouds.all}%</p>
        </Card>
    )
};

const RenderWeatherPresent: FC<RenderWeatherPresentProps>  = ({dataList}) => {

    return (
        <Weather>
            {
                dataList.map((value: DayData) =>
                    <WeatherCard dayData={value}/>
                )
            }
        </Weather>
    );
};

export default RenderWeatherPresent;